import React from 'react';
import { 
    FaArrowCircleRight, 
    FaListAlt,
    FaCalendarCheck,
    FaClock, 
    FaRocket,
    FaFire,
    FaFileAlt } from 'react-icons/fa';


export const Features = () => {
    return (
        <section id="features" className="features">
            <div className="container">
                <div className="row">
                <div className="features-inner">
                    <div className="features-content">
                    <h3>Always be in the zone with Zeros.</h3>
                    <p>
                    Getting things done don’t have to be hard anymore. Zeros provide the support you need to get back on track.
                    </p>
                    <div className="btn-row">
                        <a href="/"
                        >View All Features
                        <FaArrowCircleRight/></a>
                    </div>
                    </div>
                    <div className="features-list">
                    <ul>
                        <li>
                        {/* <FaListAlt/> */}
                        <h5> &#x23F3;	Time-Blocking</h5>
                        <p>
                        Set aside different blocks of time for each of your daily activities. 
                        Break those activities down into smaller tasks, and check each task off the list when it’s complete. 
                        </p>
                        </li>
                        <li>
                        {/* <FaCalendarCheck/> */}
                        <h5> &#x1F4C5; Deadlines Tracker</h5>
                        <p>
                        We don’t just show you the due date. Know how much time left you have to complete your tasks. 
                        </p>
                        </li>
                        <li>
                        {/* <FaRocket/> */}
                        <h5> &#x1F680; Dynamic Scheduling</h5>
                        <p>
                        Adjust your daily schedule easily by shifting the time blocks. 
                        Our friendly AI system will start recommending you time slots based on your habits. 
                        </p>
                        </li>
                        <li>
                        {/* <FaClock/> */}
                        <h5> &#x23F1; Time-Tracking</h5>
                        <p>
                        Finish your tasks for each time block within the time limit you set. 
                        The time taken for each time block will be adjusted accordingly once you end the time block. 
                        </p>
                        </li>
                        <li>
                        {/* <FaFire/> */}
                        <h5> &#x1F525; Streaks</h5>
                        <p>
                        Get better at resisting the temptation to skip a task each day you continue your streak. 
                        Replace those negative thoughts with an “I got this” attitude. 
                        </p>
                        </li>
                        <li>
                        {/* <FaFileAlt/> */}
                        <h5> &#x1F4D1; Time-Block Reports</h5>
                        <p>
                        Get an in depth analysis of how you are spending your time each day. 
                        Find out how much time you used per time block and how much free time you accumulated everyday. 
                        </p>
                        </li>
                    </ul>
                    </div>
                </div>
                </div>
            </div>
        </section>
    )
}
